/**
 * Filters the dsaData array to get problems matching a specific topic (case-insensitive).
 * @param {string} topic - The topic to filter by.
 * @returns {Array<ProblemData>} An array of problems matching the topic.
 */
export function getProblemsByTopic(topic) {
  return dsaData.filter(
    (problem) => (problem.topic || "").toLowerCase() === topic.toLowerCase()
  );
}

/**
 * Filters the dsaData array to get problems matching a specific difficulty (case-insensitive).
 * @param {string} difficulty - The difficulty to filter by.
 * @returns {Array<ProblemData>} An array of problems matching the difficulty.
 */
export function getProblemsByDifficulty(difficulty) {
  return dsaData.filter(
    (problem) =>
      (problem.difficulty || "").toLowerCase() === difficulty.toLowerCase()
  );
}

/**
 * @typedef {typeof dsaData[0]} ProblemData - Defines the structure of a problem object.
 */

/**
 * Filters problems by topic and then sorts them by difficulty (using `difficultyOrder`) and then by title.
 * @param {string} topic - The topic to filter by.
 * @returns {Array<ProblemData>} An array of problems matching the topic, sorted.
 */
export function getProblemsByTopicOrdered(topic) {
  return dsaData
    .filter(
      (problem) => (problem.topic || "").toLowerCase() === topic.toLowerCase()
    )
    .sort(
      (a, b) =>
        difficultyOrder.indexOf(a.difficulty) -
          difficultyOrder.indexOf(b.difficulty) ||
        (a.title || "").localeCompare(b.title || "")
    );
}

